.grid-container{
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: 50fr 100vw 50fr;
  grid-template-rows: auto auto auto 100fr;
  overflow: hidden;
  grid-template-areas:
    ". header ."
    "nav nav nav"
    ". player ."
    "main main main"
    // ". footer .";
}

