header.site-header{
  grid-area: header;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
  .about-container{
    padding: 1.5rem;
  }
  border-bottom: 1px solid black;
}

.player-container{
  grid-area: player;
  // display: flex;
  // flex-direction: row;
  // flex-wrap: wrap;
  // align-items: left;
  // justify-content: flex-start;
}

nav{
  grid-area: nav;
}

div.entries-container{
  grid-area: main;
  align-self: center;
  width: auto;
  height: 100%;
  overflow: scroll;
  .entries-wrap{
    height: auto;
    position:relative;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: flex-start;
    justify-content: flex-start;
  }
}

